// app/api/test-supa/route.ts
import { NextResponse } from 'next/server';
import { supabase } from '../../../lib/supabase';

export async function GET() {
  // просто чтобы увидеть, к какому проекту мы реально коннектимся
  console.log('[TEST_SUPA] URL =', process.env.NEXT_PUBLIC_SUPABASE_URL);

  const { data, error } = await supabase
    .from('users')
    .insert([
      {
        telegram_id: 'test-123',
        username: 'test_user',
        first_name: 'Test',
      },
    ])
    .select();

  console.log('[TEST_SUPA] insert data =', data);
  console.log('[TEST_SUPA] insert error =', error);

  if (error) {
    return NextResponse.json(
      { ok: false, message: error.message },
      { status: 500 },
    );
  }

  return NextResponse.json({ ok: true, data });
}
